import React, { Component } from 'react'
import axios from 'axios'

export default class SingleMovie extends Component {
    state = {
        movie: {},
        movieDetails: {}
    }
    async componentDidMount() {
        this.fetchData()
    }

    fetchData = async () => {
        const userId = this.props.match.params.user_id
        const movieId = this.props.match.params.id
        const responseMovie = await axios.get(`/api/users/${userId}/movies/${movieId}`)
        // console.log(responseMovie.data)
        this.setState({ movie: responseMovie.data })
        await this.fetchMovieDetails()

    }

    fetchMovieDetails = async () => {
        const movie_id = this.state.movie.movie_id
        const responseMovieDetails = await axios.get(`https://api.themoviedb.org/3/movie/${movie_id}?api_key=0832ef538fd529b929aeda8e57b1c0ed`)
        // console.log(responseMovieDetails.data)
        this.setState({ movieDetails: responseMovieDetails.data })
    }

    render() {
        const movie = this.state.movie
        const movieDetails = this.state.movieDetails
        const genres = movieDetails.genres
        console.log(genres)
        // const selectGenres = genres.map(genre => genre.name)

        return (
            <div>
                <p>movie id:{movie.movie_id}</p>
                {/* <p>Genre(s):{selectGenres}</p> */}
                <p>Budget: {movieDetails.budget} </p>
            </div>
        )
    }
}
